import uniRequest from '@/utils/uni-request';
export default {
	data() {
		return {
			username: '',
			password: ''
		};
	},
	methods: {
		onChange(e, type) {
			this[type] = e.target.value;
		},
		registBtn() {
			const {
				username,
				password
			} = this;
			uniRequest.post('/regist',{username:username,password:password})
		},
		appOAuthLogin(e) {
			var me = this;
			// 获取用户的登录类型
			var logintype = e.currentTarget.dataset.logintype;

			// 授权登录
			uni.login({
				provider: logintype,
				success(loginRes) {

					// 授权登录成功以后，获取用户的信息
					uni.getUserInfo({
						provider: logintype,
						success(info) {
							// console.log(JSON.stringify(info));

							var userInfo = info.userInfo;
							var face = "";
							var nickname = "";
							var openIdOrUid = "";
							if (logintype == "weixin") {
								face = userInfo.avatarUrl;
								nickname = userInfo.nickName;
								openIdOrUid = userInfo.openId;
							} else if (logintype == "qq") {
								openIdOrUid = userInfo.openId;
								nickname = userInfo.nickname;
								face = userInfo.figureurl_qq_2;
							} else if (logintype == "sinaweibo") {
								openIdOrUid = userInfo.id;
								nickname = userInfo.nickname;
								face = userInfo.avatar_large;
							}

							// 调用开发者后台，执行一键注册或登录
							uni.request({
								url: me.serverUrl + "/appUnionLogin/" + logintype,
								data: {
									"openIdOrUid": openIdOrUid,
									"nickname": nickname,
									"face": face
								},
								method: "POST",
								success(result) {
									if (result.data.status == 200) {
										var userInfo = result.data.data;
										// 保存用户信息到全局的缓存中
										uni.setStorageSync("globalUser", userInfo);
										// 切换页面跳转，使用tab切换的api
										uni.switchTab({
											url: "../me/me"
										});
									}
								}
							})

						}
					})

				}
			});
		},
		// 实现在微信小程序端的微信登录
		wxLogin(e) {
			var me = this;
			// console.log(e);
			// 通过微信开发能力，获得微信用户的基本信息
			var userInfo = e.detail.userInfo;

			// 实现微信登录
			uni.login({
				provider: "weixin",
				success(loginResult) {
					// console.log(loginResult);
					// 获得微信登录的code：授权码
					var code = loginResult.code;
					// 设置登录到哪个对于的微信小程序，大家可以根据自己的后端去实现业务参数
					// [0:NEXT超英预告][1:超英预告][2:NEXT学院电影预告]
					var loginToWhichMP = 1;
					uni.request({
						url: me.serverUrl + "/mpWXLogin/" + code,
						data: {
							"avatarUrl": userInfo.avatarUrl,
							"nickName": userInfo.nickName,
							"whichMP": loginToWhichMP
						},
						method: "POST",
						success(userResult) {
							console.log(userResult);
							var userInfo = userResult.data.data;
							// 保存用户信息到全局的缓存中
							uni.setStorageSync("globalUser", userInfo);
							// 切换页面跳转，使用tab切换的api
							uni.switchTab({
								url: "../me/me"
							});
						}
					});

				}
			})
		},
		formSubmit(e) {
			var me = this;
			var username = e.detail.value.username;
			var password = e.detail.value.password;
			// 				console.log(username);
			// 				console.log(password);

			// 发起注册/登录的请求
			var serverUrl = me.serverUrl;
			uni.request({
				url: `${serverUrl}/user/registOrLogin`,
				data: {
					"username": username,
					"password": password
				},
				method: "POST",
				success: (res) => {
					// 获取真实数据之前，务必判断状态是否为200
					if (res.data.code == 0) {
						var userInfo = res.data.data;
						// console.log(userInfo);
						// 保存用户信息到全局的缓存中
						uni.setStorageSync("globalUser", userInfo);
						// 切换页面跳转，使用tab切换的api
						uni.switchTab({
							url: "../me/me"
						});
					} else {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							image: "../../static/icos/error.png"
						})
					}
				}
			});
		}
	}
}
